import React from 'react';
import Card from '../Card/Card.component';

// CardList Component
const CardList = ({robots}) => {
  return(
    <div className='card-list'>
      {
        robots.map(({id, name, email}) => {
          return(
            <Card key={id} id={id} name={name} email={email} />)
        })
      }
    </div>
  );
}
export default CardList;