import React, { Component } from 'react';
import CardList from '../Components/CardsList/CardsList.component';
import 'tachyons';
// import { robots } from './robots';
import SearchBox from '../Components/SearchBox/SearchBox.component';
import './App.css';
import Scroll from '../Components/Scroll/Scroll.component';
import ErrorBoundry from '../Components/ErrorBoundry/ErrorBoundry.component';

class App extends Component {
	constructor() {
		super();
		this.state = {
			robots: [],
			searchField: '',
		};
	}

	componentDidMount() {
		fetch('https://jsonplaceholder.typicode.com/users')
			.then((response) => response.json())
			.then((users) => this.setState({ robots: users }));
	}

	onSearchChange = (event) => {
		this.setState({ searchField: event.target.value });
	};

	render() {
		const { robots, searchField } = this.state;
		const filteredRobots = robots.filter((robot) => {
			return robot.name.toLocaleLowerCase().includes(searchField.toLowerCase());
		});

		return !robots.length === 0 ? (
			<h1>Loading robots...</h1>
		) : (
			<div className='app tc'>
				<h1 className='f1'>Robo Friends</h1>
				<SearchBox type='text' placeholder='Search robots' onChange={this.onSearchChange} />
				<Scroll>
					<ErrorBoundry>
						<CardList robots={filteredRobots} />
					</ErrorBoundry>
				</Scroll>
			</div>
		);
	}
}
export default App;
